$headerheight: 64px;
$mobileheaderheight:48px;

#site-header{
    display:flex;
    width:100%;
    height:$headerheight;
    position: relative;
    padding-left:$edgeclamp;
    padding-right:$edgeclamp;
    background-color:$primary;

    .mobile-menu-button{
        display:none;
        background-image:url(../../assets/icons/hamburger-button.svg);
        height: 100%;
        aspect-ratio: 1/1;
        background-repeat: no-repeat;
        background-position: center center;
        cursor:pointer;
    }
    .mobile-menu-container{
        display:none;
    }
    .top.menu-logo{
        height:100%;
        .site-branding{
            height: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            a{
                color:$black;
                text-decoration:none;
            }
            h1{margin:0;}
            img{
                max-height:100%;
            }
        }
    }
    .top.menu-container{
        display: flex;
        align-items: center;
        ul{
            list-style-type:none;
            margin:0;
            display: inline-flex;
            gap: 12px;
        }
        .primary-menu-container{
            padding-left:11vw;
        }
    }
}

@media screen and (max-width:991px){
    #site-header{
        height:$mobileheaderheight;
        .mobile-menu-button{
            display: inline-flex;
        }
        .desktop-menu{
            display:none;
        }
    }
}